// Constants used in EdgeFunction*.ts and sdk.js
// The event constants are copied from sdk/consts.js
export const EVENT_ERROR = -1
export const EVENT_IO_FETCH = 1
export const EVENT_IO_RESPONSE_CHUNK = 2

export function eventToString(event: number): string {
  switch (event) {
    case EVENT_ERROR:
      return 'EVENT_ERROR'
    case EVENT_IO_FETCH:
      return 'EVENT_IO_FETCH'
    case EVENT_IO_RESPONSE_CHUNK:
      return 'EVENT_IO_RESPONSE_CHUNK'
    default:
      return `Unknown event: ${event}`
  }
}

export const SEMAPHORE_INDEX_WORKER_WAIT = 0
export const SEMAPHORE_VALUE_SHOULD_WAIT = 0
export const SEMAPHORE_VALUE_SHOULD_WAKE = 1
